import React from 'react';
import LandingLayout from '../../layouts/LandingLayout';
import { connect } from 'react-redux';
import MaterialTable, { MTableToolbar } from 'material-table';
import { TextField, Fab, Chip } from '@material-ui/core';
import { productActions } from '../../store/actions/product.action';
import AssignmentTurnedInIcon from '@material-ui/icons/AssignmentTurnedIn';

function CarDetail(props) {
	const modelStructure = {
		columns: [
			{
				title: 'Avatar',
				field: 'imageUrl',
				render: rowData => <img src={mergeUrl(rowData.image)} style={{ width: 45 }} />
			},
			{ title: 'Brand', field: 'brand.name' },
			{ title: 'Model', field: 'model.name' },
			{
				title: 'Cant',
				field: 'cant',
				render: rowData => (
					<TextField
						autoFocus
						onChange={event => {
							changeCant(rowData._id, event.target.value);
						}}
						value={rowData.cant}
						margin='dense'
						id='cant'
						label='Cant'
						type='number'
						fullWidth
					/>
				)
			},
			{ title: 'Price', field: 'price' },
			{
				title: 'Total',
				field: 'total',
				render: rowData => rowData.cant * rowData.price
			}
		]
	};
	const [listProd, setListProd] = React.useState([]);
	const [address, setAddress] = React.useState('');
	React.useEffect(() => {
		setListProd(props.listCarProduct);
	}, [props.listCarProduct]);

	const changeCant = (id, value) => {
		if (value > 0) {
			let filter = listProd.map(item => {
				if (item._id == id) {
					item.cant = value;
				}
				return item;
			});
			setListProd(filter);
		}
	};

	const confirmShop = () => {
		let data = {};
		data.listProd = listProd;
		data.total = totalPrice();
		data.address = address;
		props.handleConfirmCarDetail(data);
	};

	const totalPrice = () => {
		let totalPrice = 0;
		listProd.forEach(element => {
			totalPrice += element.cant * element.price;
		});
		return totalPrice;
	};

	const mergeUrl = image => {
		if (image) {
			return `${process.env.REACT_APP_API_HOST}` + image;
		} else {
			return 'data:image/svg+xml;charset=UTF-8,%3Csvg%20width%3D%22288%22%20height%3D%22225%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%20288%20225%22%20preserveAspectRatio%3D%22none%22%3E%3Cdefs%3E%3Cstyle%20type%3D%22text%2Fcss%22%3E%23holder_164edaf95ee%20text%20%7B%20fill%3A%23eceeef%3Bfont-weight%3Abold%3Bfont-family%3AArial%2C%20Helvetica%2C%20Open%20Sans%2C%20sans-serif%2C%20monospace%3Bfont-size%3A14pt%20%7D%20%3C%2Fstyle%3E%3C%2Fdefs%3E%3Cg%20id%3D%22holder_164edaf95ee%22%3E%3Crect%20width%3D%22288%22%20height%3D%22225%22%20fill%3D%22%2355595c%22%3E%3C%2Frect%3E%3Cg%3E%3Ctext%20x%3D%2296.32500076293945%22%20y%3D%22118.8%22%3EThumbnail%3C%2Ftext%3E%3C%2Fg%3E%3C%2Fg%3E%3C%2Fsvg%3E'; // eslint-disable-line max-len
		}
	};

	return (
		<LandingLayout>
			<main style={{ marginTop: '64px' }}>
				<div
					style={{
						display: 'flex',
						justifyContent: 'flex-end',
						alignItems: 'center',
						marginBottom: '10px'
					}}
				>
					<div style={{ flexGrow: 1 }}>
						<TextField
							autoFocus
							onChange={event => {
								setAddress(event.target.value);
							}}
							value={address}
							margin='dense'
							id='address'
							label='Address for receive'
							fullWidth
						/>
					</div>
					<Fab
						size='medium'
						color='primary'
						aria-label='Add'
						disabled={address.length == 0 || listProd.length == 0}
						onClick={() => {
							confirmShop();
						}}
					>
						<AssignmentTurnedInIcon />
					</Fab>
				</div>
				<MaterialTable
					title='Detail Car'
					columns={modelStructure.columns}
					data={listProd}
					localization={{ body: { editRow: { deleteText: 'Are you sure delete this phone?' } } }}
					options={{
						search: false,
						actionsColumnIndex: -1
					}}
					components={{
						Toolbar: props => (
							<div
								style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}
							>
								<div style={{ flexGrow: '1' }}>
									<MTableToolbar {...props} />
								</div>
								<div style={{ padding: '0px 10px' }}>
									<Chip
										label={`Total ${totalPrice()}`}
										color='secondary'
										style={{ marginRight: 5 }}
									/>
								</div>
							</div>
						)
					}}
					editable={{
						onRowDelete: oldData =>
							new Promise((resolve, reject) => {
								setTimeout(async () => {
									{
										const data = [...listProd];
										const index = data.indexOf(oldData);
										data.splice(index, 1);
										props.handleDeleteProdCar(data);
										await setListProd(data);
										resolve();
									}
									resolve();
								}, 1000);
							})
					}}
				/>
			</main>
		</LandingLayout>
	);
}
const mapStateToProps = ({ productReducer }) => {
	return {
		listCarProduct: productReducer.listCarProduct
	};
};
const mapDispatchToProps = dispatch => {
	return {
		handleDeleteProdCar: product => {
			dispatch(productActions.DeleteProductCar(product));
		},
		handleConfirmCarDetail: data => {
			dispatch(productActions.ConfirmShop(data));
		}
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(CarDetail);
